'use strict';

// MODULES //

var NODE_VERSION = require( '@stdlib/process/node-version' );


// MAIN //

var MAJOR = ( NODE_VERSION ) ? parseInt( NODE_VERSION.split( '.' )[ 0 ], 10 ) : -1;


// EXPORTS //

module.exports = MAJOR;
